caption {
  text-align: left;
}

td,
th {
  vertical-align: top;
  padding: 0;
}

th {
  text-align: left;
  font-weight: bold;
}

.header {
  position: relative;
  height: 37.5rem;
}

.header-bg {
  background-image: url(../img/mainvisual.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header-logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.nav {
  padding: 1.25rem 2.5rem 0 0;
}

.nav-list {
  display: flex;
  align-items: center;
  justify-content: end;
}

.nav-item:not(:first-of-type) {
  margin-left: 1.875rem;
}

.nav-link {
  font-size: 0.9rem;
  font-weight: bold;
  color: var(--base-color);
  line-height: 1.7;
}

.menu {
  margin-top: 0.625rem;
}

.menu-list-body {
  margin-top: clamp(1.875rem, -0.2596153846rem + 9.1076923077vw, 11.125rem);
}

@media (min-width: 992px) {
  .menu-list-wrapper {
    display: flex;
    justify-content: center;
  }
}

.menu-list-box {
  margin-bottom: 2.1875rem;
}
@media (min-width: 992px) {
  .menu-list-box {
    margin-bottom: 0;
    padding: 0 2.8125rem;
  }
}

@media (min-width: 992px) {
  .menu-list-box--coffee,
  .menu-side-list-wrapper {
    width: 50%;
  }
}

@media (min-width: 992px) {
  .menu-list-box--other {
    margin-top: 5rem;
  }
}

.menu-list {
  margin-top: clamp(1.5625rem, 0.9134615385rem + 2.7692307692vw, 4.375rem);
}

.menu-data-wrap {
  margin-bottom: 1.5625rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu-list-name,
.menu-list-price {
  font-weight: 500;
}

.menu-list-name {
  width: 85%;
  line-height: 1.5;
  border-bottom: 0.0625rem dashed #000;
}
@media (min-width: 768px) {
  .menu-list-name {
    width: 90%;
  }
}

.menu-list-price {
  padding-top: 1.25rem;
  font-size: 0.9rem;
}

.about {
  margin-top: clamp(7.1875rem, 6.3942307692rem + 3.3846153846vw, 10.625rem);
}

.about-body {
  margin-top: clamp(1.875rem, 1.0096153846rem + 3.6923076923vw, 5.625rem);
}

.about-article-body {
  margin-top: clamp(4.3125rem, 4.0096153846rem + 1.2923076923vw, 5.625rem);
}

.about-article-wrapper {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(1, 1fr);
  row-gap: clamp(1.25rem, 0.9615384615rem + 1.2307692308vw, 2.5rem);
}
@media (min-width: 992px) {
  .about-article-wrapper {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2.5rem;
  }
}

.about-article {
  font-size: clamp(0.875rem, 0.8461538462rem + 0.1230769231vw, 1rem);
  line-height: 1.5;
}

.location {
  margin-top: clamp(7.1875rem, 6.3942307692rem + 3.3846153846vw, 10.625rem);
}

.location-map-body {
  margin-top: clamp(1.875rem, 1.0096153846rem + 3.6923076923vw, 5.625rem);
}

.location-map {
  margin-top: clamp(1.5625rem, 0.9134615385rem + 2.7692307692vw, 4.375rem);
}

.location-map > iframe {
  width: 100%;
  height: 25rem;
  aspect-ratio: 16/9;
}

.location-info {
  margin-top: 1.25rem;
}

.location-address {
  margin-bottom: 0.625rem;
}

.location-address > p {
  font-size: clamp(0.875rem, 0.8461538462rem + 0.1230769231vw, 1rem);
  line-height: 1.5;
}

.section-title {
  font-size: clamp(2rem, 1.4230769231rem + 2.4615384615vw, 4.5rem);
  font-weight: bold;
  color: var(--base-color);
  line-height: clamp(3.4rem, 2.4192307692rem + 4.1846153846vw, 7.65rem);
}

.section-head-wrap {
  text-align: center;
}

.section-head {
  font-size: clamp(1.25rem, 0.9615384615rem + 1.2307692308vw, 2.5rem);
  font-weight: bold;
  line-height: 1.7;
}

.container {
  max-width: 60.5rem;
  margin: 0 auto;
  padding: 0 0.9375rem;
}

.fixed-bg {
  position: relative;
  height: clamp(5.875rem, 2.9038461538rem + 12.6769230769vw, 18.75rem);
}

.fixed-bg-attachment {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  display: block;
  height: 100%;
  width: 100%;
}

.fixed-bg-title {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  transform: translate(-50%, -50%);
}

.fixed-bg-image {
  display: block;
  position: relative;
  overflow: hidden;
  clip-path: inset(0 0 0 0);
  height: 100%;
}

.fixed-bg-image > img {
  position: fixed;
  width: 100%;
  min-height: 100vh;
  height: auto;
  left: 0;
  top: 0;
  pointer-events: none;
}

:root {
  --base-color: #FFFF;
  --text-color: #000;
  --line-color: #E03131;
}

.theme-under-line {
  display: inline-block;
  border-bottom: 0.375rem solid var(--line-color);
}